.headerParent h2 {
    color: var(--darkViolet);
    margin: 5rem 5rem 0 5rem;
    text-align: center;
    border-top: 2px solid var(--lightGreen);
    border-bottom: 2px solid var(--lightGreen); 
}

.headerParent span {
    font-family: 'Permanent Marker', sans serif;
    color: var(--lightViolet);
}

